}

.efficiency-metrics {
  margin-top: 2.5rem;
  background: linear-gradient(135deg, white 0%, var(--light-gray) 100%);
  padding: 2rem;
  border-radius: 16px;
  box-shadow: var(--box-shadow-subtle);
  transition: var(--transition);
}

.efficiency-metrics:hover {
  transform: translateY(-5px);
  box-shadow: var(--box-shadow);
}

.efficiency-metrics h4 {
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
  color: var(--dark-color);
  position: relative;
  display: inline-block;
}

.efficiency-metrics h4::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 3px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  border-radius: 2px;
}

.metrics-row {
  display: flex;
  justify-content: space-around;
}

.metric {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.metric-value {
  font-size: 2.2rem;
  font-weight: 700;
  background: linear-gradient(45deg, var(--success-color), var(--secondary-color));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0.5rem;
}

.metric-label {
  font-size: 1rem;
  color: var(--dark-color-light);
  font-weight: 500;
}

.error-container {
  text-align: center;
  color: var(--accent-color);
  padding: 3rem;
  animation: fadeInUp 0.5s ease-out;
}

.error-icon {
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
}

.error-message {
  margin-top: 1rem;
  font-size: 1.2rem;
  font-weight: 500;
}

.error-details {
  margin-top: 1.5rem;
  color: var(--dark-color-light);
  background-color: var(--light-gray);
  padding: 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-family: monospace;
  white-space: pre-wrap;
  text-align: left;
  max-height: 200px;
  overflow-y: auto;
}

