.about-text {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.7) 100%);
  backdrop-filter: blur(10px);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: var(--box-shadow-large), inset 0 1px 1px rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.about-text::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle at 30% 30%, rgba(67, 97, 238, 0.05) 0%, transparent 70%);
  z-index: -1;
}

.about-text p {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  color: var(--dark-color);
  line-height: 1.8;
  position: relative;
}

.about-text p:first-of-type {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--primary-dark);
  margin-bottom: 2rem;
}

.about-text p:first-of-type::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 60px;
  height: 3px;
  background: linear-gradient(to right, var(--primary-color), var(--primary-light));
  border-radius: 3px;
}

.about-text ul {
  margin: 0 0 2rem 1.2rem;
  position: relative;
}

.about-text li {
  margin-bottom: 1rem;
  padding-left: 10px;
  color: var(--dark-color);
  position: relative;
}

.about-text li::before {
  content: '';
  position: absolute;
  top: 0.65rem;
  left: -1.2rem;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-light) 100%);
  box-shadow: 0 2px 8px rgba(67, 97, 238, 0.4);
}

.tech-stack {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  padding: 3rem;
  border-radius: 20px;
  box-shadow: var(--box-shadow-large);
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(203, 213, 225, 0.4);
  transition: transform 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275), box-shadow 0.3s ease;
  z-index: 1;
}

.tech-stack::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 10% 90%, rgba(76, 201, 240, 0.1) 0%, transparent 30%),
    radial-gradient(circle at 90% 10%, rgba(67, 97, 238, 0.1) 0%, transparent 30%);
  z-index: -1;
}

.tech-stack::after {
  content: '{ ... }';
  position: absolute;
  bottom: 10px;
  right: 15px;
  font-size: 4rem;
  font-family: monospace;
  color: rgba(67, 97, 238, 0.05);
  pointer-events: none;
}

.tech-stack:hover {
  transform: translateY(-15px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.1);
}

.tech-stack h3 {
  font-size: 1.8rem;
  margin-bottom: 2.5rem;
  color: var(--dark-color);
  text-align: center;
  position: relative;
}

.tech-stack h3::after {
  content: '';
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  border-radius: 2px;
}

.tech-table-container {
  position: relative;
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(203, 213, 225, 0.3);
  backdrop-filter: blur(5px);
}

.tech-table {
  width: 100%;
  border-collapse: collapse;
}

.tech-table tr {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.tech-table tr:hover {
  transform: translateX(5px);
}

.tech-table td {
  padding: 15px 20px;
  border-bottom: 1px solid rgba(203, 213, 225, 0.4);
  transition: background-color 0.3s ease;
}

.tech-table tr:hover td {
  background-color: rgba(67, 97, 238, 0.05);
}

.tech-table tr:last-child td {
  border-bottom: none;
}

.tech-table td:first-child {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
